.headerCheckout{
    width: 100%;
    height: 200px;
    background-color: #f9f9f9;
    transform: translateY(-50px);
    z-index: -1423;
    padding-top: 90px !important;
    position: sticky;
    display: flex;
    padding: 0px 50px;
    align-items: center;
    justify-content: space-between;
}
.iconHome{
    font-size: 40px;
    color: black !important;
}

.arrowRight{
    font-size: 38px;
}
.number{
    font-size: 25px;
    background-color: #01e281;
    font-weight: 900;
    padding: 10px;
    text-align: center;
    width: 70px;
    height: 70px;
    border-radius: 50%;
}
.arrowch{
    font-size: 40px;
    opacity: 0.5;
}



/* للشاشات الكبيرة */
@media (min-width: 1200px) and (max-width: 1700px) {
    .headerCheckout {
        height: 160px;
        padding: 0px 30px;
    }
    .iconHome {
        font-size: 34px;
    }
    .arrowRight, .arrowch {
        font-size: 32px;
    }
    .number {
        width: 50px;
        height: 50px;
        font-size: 15px;
    }
}
@media (min-width: 1200px) {
    .headerCheckout {
        height: 180px;
        padding: 0px 40px;
    }
    .iconHome {
        font-size: 36px;
    }
    .arrowRight, .arrowch {
        font-size: 35px;
    }
    .number {
        width: 65px;
        height: 65px;
        font-size: 22px;
    }
}

/* للشاشات المتوسطة */
@media (min-width: 992px) and (max-width: 1199px) {
    .headerCheckout {
        height: 160px;
        padding: 0px 30px;
    }
    .iconHome {
        font-size: 34px;
    }
    .arrowRight, .arrowch {
        font-size: 32px;
    }
    .number {
        width: 60px;
        height: 60px;
        font-size: 20px;
    }
}

/* للشاشات الصغيرة */
@media (min-width: 768px) and (max-width: 991px) {
    .headerCheckout {
        height: 150px;
        padding: 0px 20px;
    }
    .iconHome {
        font-size: 30px;
    }
    .arrowRight, .arrowch {
        font-size: 28px;
    }
    .number {
        width: 55px;
        height: 55px;
        font-size: 18px;
    }
}

/* للشاشات الصغيرة جدًا */
@media (min-width: 468px) and (max-width: 767px) {
    .headerCheckout {
        height: 140px;
        display: flex;
        transform: translateY(-62px);
        padding: 0px 15px;
        text-align: center;
        flex-direction: column;
        justify-content: center;
        align-items: start;
        padding-bottom: 50px;
    }
    .iconHome {
        font-size: 28px;
    }
    .arrowRight, .arrowch {
        font-size: 20px;
    }
    .number {
        width: 38px;
        height: 36px;
        font-size: 10px;
    }
    .flexNumberandText{
        gap: 15px !important;
    }
    .flexheadr{
        width: 100% !important;
        display: flex !important;
        align-items: center !important;
        gap: 0px !important;
        padding-left: 0px !important;
        margin-top: 0px !important;
    }
    .h1right{
        width: 516px;
        display: flex;
        align-items: center;
        gap: 20px;
    }
    .fontSize{
        font-size: 17px !important;
    }
}
@media screen and (max-width: 467px) {
    .headerCheckout {
        height: 140px;
        display: flex;
        transform: translateY(41px);
        padding: 0px 15px;
        text-align: center;
        flex-direction: column;
        justify-content: center;
        align-items: start;
        padding-bottom: 50px;
    }
    .iconHome {
        font-size: 24px;
    }
    .arrowRight, .arrowch {
        font-size: 17px;
    }
    .number {
        width: 30px;
        height: 33px;
        font-size: 14px;
    }
    .flexNumberandText{
        gap: 2px !important;
    }
    .flexheadr{
        width: 97% !important;
        display: flex !important;
        align-items: center !important;
        gap: 0px !important;
        padding-left: 0px !important;
    }
    .fontSize{
        font-size: 13px !important;
    }
}

